.navbar {
    position: fixed;
    z-index: 99;
    width: 100%;
    ::before {
        position: absolute;
        content: '';
        //background: rgba(214, 48, 49,1.0)
        width: 100%;
        z-index: -1;
        height: 100%; }
    &_wrap_title {
        margin: 0 !important;
        padding: 0 !important; }
    &_wrap_ul {
        list-style: none;
        display: flex;
        justify-content: space-between;
        align-items: center; }
    &_wrap_li {
        padding-left: 20px;
        padding-right: 20px;
        position: relative; }
    &_wrap_navbar_links {
        text-decoration: none;
        color: #ffffff;
        position: relative;
        font-size: 18px; }
    &_wrap_navbar_links::before {
        width: 100%;
        height: 0.6px;
        background: #ffffff;
        content:  '';
        opacity: 1;
        transform: scale(0);
        position: absolute;
        transform-origin: left center;
        border-radius: 20px;
        bottom: -5px;
        transition: 0.5s ease; }
    &_wrap_navbar_links:hover::before {
        transform: scale(1);
        opacity: 0.4;
        transition: 0.5s ease; }
    &_wrap {
        min-height: 100px;
        flex-basis: 50%;
        flex-wrap: wrap;
        display: flex;
        position: relative;
        justify-content: center;
        align-items: center; }
    &_wrap::before {
        position: absolute;
        content: '';
        background: rgba(45, 52, 54,.4);
        width: 100%;
        z-index: -1;
        height: 250%;
        transform: translateY(-60px);
        border-bottom-left-radius: 100%;
        border-bottom-right-radius: 100%; }
    &_wrap_title {
        text-transform: uppercase;
        color: #ffffff;
        font-size: 28px;
        position: relative; }
    &_wrap_title_subtitle {
        background: url('../img_vendor/flower.png');
        background-size: cover;
        width: 320px;
        height: 200px;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-90deg);
        position: absolute;
        z-index: -1;
        content: '';
        opacity: 0.9; } }







