.features {
    display: flex;
    position: relative;
    align-items: center;
    padding: 200px 0;
    background: linear-gradient(to right, #3a6186, #89253e);
    &_section_image {
        background: url('../img_vendor/heartcrystal.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
        width: 50%;
        left: -10%;
        opacity: 0.9;
        top: 0;
        height: 100%;
        position: absolute; }
    &_section_image::before {
        content: '';
        position: absolute;
        right: -20%;
        transform: translateX(50%);
        background: #ffffff;
        width: 1px;
        opacity: 0.7;
        height: 100%; }
    &_wrap {
        width: 400px;
        float: right; }
    &_wrap_title {
        font-size: 48px;
        @include great;
        text-align: center;
        position: relative; }
    &_wrap_title::before {
        bottom: -20px;
        content: '';
        position: absolute;
        width: 50px;
        height: 2px;
        background: #ffffff;
        transform: translate(-50%);
        left: 50%; }
    &_wrap_subtitle {
        text-align: center;
        @include great;
        font-size: 48px; } }
