.hero {
    @include luna;
    &_wrap {
        text-align: center;
        padding-top: 250px; }
    &_wrap_subtitle {
        line-height: 60px;
        position: relative; }
    &_wrap_description {
        margin-top: 55px;
        //padding-top: 100px
 }        //transform: translateY(100px)
    &_wrap_separator {
        margin-top: 55px;
        position: relative; }
    &_wrap_separator::before {
        opacity: 0.7;
        top: 50%;
        transform: translate(-45px, -50%);
        content: '';
        right: 50%;
        position: absolute;
        width: 80px;
        height: 2px;
        background: #ffffff; }
    &_wrap_separator::after {
        opacity: 0.7;
        top: 50%;
        left: 50%;
        transform: translate(45px, -50%);
        content: '';
        position: absolute;
        width: 80px;
        height: 2px;
        background: #ffffff; }
    &_fa_heart {
        opacity: 0.7; }
    &_fa_mouse {
        opacity: 0.7; }
    &_wrap_mouse {
        perspective: 10000px;
        margin-top: 55px;
        animation: mouse 5s ease infinite; } }


@keyframes mouse {
    0% {
        transform: translateY(0px); }
    50% {
        transform: translateY(35px); }
    100% {
        transform: translate(0px); } }
