.slider {
    padding: 45px 0;
    position: relative;
    box-shadow: 0px 0px 15px #0c0c0c;
    background: linear-gradient(45deg, rgba(240, 194, 123, 0.5), rgba(75, 18, 72, 0.5)), url('../img_vendor/love-bg.jpg');
    background-size: cover;
    &_slick_wrap_image {
        width: 533.33px;
        height: 300px;
        border-radius: 40px; }
    &_slick_wrap_image_second {
        height: 533.33px;
        border-radius: 40px;
        width: 300px; }
    &_slick_wrap_image_third {
        border-radius: 40px;
        width: 300px;
        height: 450px; }
    &_wrap {
        border-radius: 25px;
        text-align: center; }
    &_wrap_slick_slider {
        width: 533.33px;
        margin: 0 auto;
        padding: 55px 0; }
    &_wrap_slick_second {
        width: 300px;
        margin: 0 auto; }
    &_wrap_slick_third {
        margin: 0 auto;
        width: 300px; }
    &_wrap_total {
        display: flex;
        justify-content: space-around;
        align-items: center; }
    &_wrap_title {
        font-size: 48px;
        @include great;
        position: relative; }
    &_wrap_content {
        padding-top: 25px; }
    &_wrap_title::before {
        bottom: -20px;
        content: '';
        position: absolute;
        width: 50px;
        height: 2px;
        background: #ffffff;
        transform: translate(-50%);
        left: 50%; }
    &_wrap_description {
        font-size: 48px;
        @include great;
        position: relative; }
    &_total_slider {
        visibility: hidden;

        height: 0;
        transform: scale(0); }
    &_total_slider_active {
        visibility: visible;
        height: 870px;

        transform: scale(1); } }

.prev {
    z-index: 99;
    position: absolute;
    background: rgba(253, 221, 230, 0.7);
    width: 40px;
    height: 40px;
    left: 0;
    top: 50%;
    transform: translate(-100px, -50%);
    z-index: 99;
    border-radius: 50%; }

.prev:hover {
    background: rgba(253, 221, 230, 0.8);
    transition: 0.4s ease;
    cursor: pointer; }

.next {
    z-index: 99;
    position: absolute;
    background: rgba(253, 221, 230, 0.7);
    width: 40px;
    height: 40px;
    top: 50%;
    transform: translate(100px, -50%);
    right: 0;
    z-index: 99;
    border-radius: 50%; }

.next:hover {
    background: rgba(253, 221, 230, 0.8);
    transition: 0.4s ease;
    cursor: pointer; }

.fa-long-arrow-alt-left {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute; }


.fa-long-arrow-alt-right {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute; }

.slick-slide:focus {
    outline: none; }

.fa-sort-down {
    color: rgba(45, 52, 54,1.0);
    transform: translate(-50%);
    left: 50%;
    cursor: pointer;
    position: absolute; }
