.about {
    background: linear-gradient(45deg, #333333, #dd1818);
    margin-top: 100px;
    display: flex;
    position: relative;
    align-items: center;
    padding: 200px 0;
    box-shadow: 0px 0px 5px #0c0c0c;
    &_wrap {
        //background: rgba(236, 240, 241,1.0)
        display: flex;
        justify-content: space-between;
        margin-left: 25px;
        margin-right: 25px;
        align-items: center;
        flex-wrap: wrap; }
    &_wrap_title {
        //text-transform: uppercase
        font-size: 48px;
        @include great;
        text-align: center;
        position: relative; }
    &_wrap_title::before {
        bottom: -20px;
        content: '';
        position: absolute;
        width: 50px;
        height: 2px;
        background: #ffffff;
        transform: translate(-50%);
        left: 50%; }
    &_section_image {
        background: url('../img_vendor/valentine.jpg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        width: 50%;
        right: 0;
        opacity: 0.75;
        top: 0;
        height: 100%;
        position: absolute; }
    &_wrap_subtitle {
        width: 400px;
        text-align: center;
        @include great;
        font-size: 48px; } }
