* {
    box-sizing: border-box; }

.container {
    max-width: 1170px;
    width: 100%;
    margin: 0 auto; }

.love-bg {
    position: fixed;
    background-image: linear-gradient(rgba(178, 10, 44, 0.9), rgba(44, 62, 80,.9)), url('../img_vendor/heart.webp');
    background-size: cover;
    opacity: 1;
    z-index: -1;
    width: 100%;
    perspective: 10000px;
    animation: heart 5s ease infinite;
    height: 100%; }

::selection {
    background: rgba(214, 48, 49,.2); }
@keyframes heart {
    0% {
        transform: scale3d(1, 1 , 1); }
    50% {
        transform: scale3d(1.1, 1.1 , 1.1); }
    100% {
        transform: scale3d(1, 1 , 1); } }
